import React from "react";

import {
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CSelect,
  CTextarea,
} from "@coreui/react";
import { GavelOutlined } from "@material-ui/icons";
import styles from "./styles";

function InfoDoImovel({
  inputPropertyInformation,
  setInputPropertyInformation,
}) {
  const handleChangeInput = (event) => {
    const { name, value } = event.target;
    setInputPropertyInformation({ ...inputPropertyInformation, [name]: value });
  };

  return (
    <div className="row">
      <p className="mb-3 col-xl-9 col-sm-12 col-lg-9">
        Deseja aderir ao Programa de Regularizao Ambiental - PRA, caso o
        imvel rural possua (uma das situaes a seguir, ocorrida at 22 de
        julho de 2008): necessidade de recomposio de reas de APP e de uso
        restrito; dficit referente a Reserva Legal; autuao?
      </p>
      <CInputGroup className="mb-3 col-xl-3 col-sm-12 col-lg-3">
        <CSelect
          custom
          onChange={handleChangeInput}
          value={inputPropertyInformation.pra}
          title="Deseja aderir ao Programa de Regularizao Ambiental - PRA, caso o
          imvel rural possua (uma das situaes a seguir, ocorrida at 22 de
          julho de 2008): necessidade de recomposio de reas de APP e de uso
          restrito; dficit referente a Reserva Legal; autuao?"
          name="inputPropertyInformation"
          id="select"
          required
        >
          <option value={undefined} hidden>
            Escolha uma opo
          </option>
          <option value={true}>Sim</option>
          <option value={false}>No</option>
        </CSelect>
      </CInputGroup>
      <p className="mb-3 col-xl-9 col-sm-12 col-lg-9">
        O imvel rural possu rea com dficit de vegetao nativa para fim de
        cumprimento da Reserva Legal?
      </p>
      <CInputGroup className="mb-3 col-xl-3 col-sm-12 col-lg-3">
        <CSelect
          custom
          onChange={handleChangeInput}
          value={inputPropertyInformation.deficitNativeVegetation}
          title="O imvel rural possu rea com dficit de vegetao nativa para fim de 
          cumprimento da Reserva Legal?"
          id="select"
          required
        >
          <option value={undefined} hidden>
            Escolha uma opo
          </option>
          <option value={true}>Sim</option>
          <option value={false}>No</option>
        </CSelect>
      </CInputGroup>
      <p className="mb-3 col-xl-6 col-sm-12 col-lg-6">
        Qual alternativa voc pretende adotar, isolada ou conjuntamente, para
        regularizar o dficit?
      </p>
      <CInputGroup className="mb-3 col-xl-12 col-sm-12 col-lg-12">
        <CTextarea
          name="regularizeDeficit"
          rows="6"
          title="Qual alternativa voc pretende adotar, isolada ou conjuntamente, para
          regularizar o dficit?"
          onChange={handleChangeInput}
        />
      </CInputGroup>
      <p className="mb-3 col-xl-6 col-sm-12 col-lg-6">
        Caso realize a compensao, como deseja compensar a rea com deficit?
      </p>
      <CInputGroup className="mb-3 col-xl-12 col-sm-12 col-lg-12">
        <CTextarea
          name="compensationDeficitArea"
          rows="6"
          title="Caso realize a compensao, como deseja compensar a rea com deficit?"
          onChange={handleChangeInput}
        />
      </CInputGroup>
      <p className="mb-3 col-xl-9 col-sm-12 col-lg-9">
        Existe Termo de Ajuste de Conduta (TAC) aprovado referente 
        regularizao de reas de Preservao Permanente (APP), Reserva Legal ou
        rea de Uso Restrito do Imvel, objeto de Autuao?
      </p>
      <CInputGroup className="mb-3 col-xl-3 col-sm-12 col-lg-3">
        <CSelect
          custom
          onChange={handleChangeInput}
          value={inputPropertyInformation.tac}
          name="tac"
          title="Existe Termo de Ajuste de Conduta (TAC) aprovado referente 
          regularizao de reas de Preservao Permanente (APP), Reserva Legal ou
          rea de Uso Restrito do Imvel, objeto de Autuao?"
          id="select"
          required
        >
          <option value={undefined} hidden>
            Escolha uma opo
          </option>
          <option value={true}>Sim</option>
          <option value={false}>No</option>
        </CSelect>
      </CInputGroup>
      <p className="mb-3 col-xl-9 col-sm-12 col-lg-9">
        Existem infraes cometidas anteriores a 22 de Julho de 2008, relativas
        a supresso irregular de vegetao em reas de Preservao Permanente
        (APP), Reserva Legal ou rea de Uso Restrito do Imvel, objeto de
        Autuao?
      </p>
      <CInputGroup className="mb-3 col-xl-3 col-sm-12 col-lg-3">
        <CSelect
          custom
          onChange={handleChangeInput}
          value={inputPropertyInformation.infractions}
          name="infractions"
          title="Existem infraes cometidas anteriores a 22 de Julho de 2008, relativas
          a supresso irregular de vegetao em reas de Preservao Permanente
          (APP), Reserva Legal ou rea de Uso Restrito do Imvel, objeto de
          Autuao?"
          id="select"
          required
        >
          <option value={undefined} hidden>
            Escolha uma opo
          </option>
          <option value={true}>Sim</option>
          <option value={false}>No</option>
        </CSelect>
      </CInputGroup>
      <p className="mb-3 col-xl-9 col-sm-12 col-lg-9">
        O imvel rural possu rea Remanescente de Vegetao Nativa excedente ao
        mnimo exigido para Reserva Legal?
      </p>
      <CInputGroup className="mb-3 col-xl-3 col-sm-12 col-lg-3">
        <CSelect
          custom
          onChange={handleChangeInput}
          value={inputPropertyInformation.remainingAreaNativeVegetation}
          name="infractions"
          title="O imvel rural possu rea Remanescente de Vegetao Nativa excedente ao
          mnimo exigido para Reserva Legal?"
          id="select"
          required
        >
          <option value={undefined} hidden>
            Escolha uma opo
          </option>
          <option value={true}>Sim</option>
          <option value={false}>No</option>
        </CSelect>
      </CInputGroup>
      <p className="mb-3 col-xl-6 col-sm-12 col-lg-6">
        O que voc deseja fazer com a rea excedente?
      </p>
      <CInputGroup className="mb-3 col-xl-12 col-sm-12 col-lg-12">
        <CTextarea
          name="surplusArea"
          rows="6"
          title="O que voc deseja fazer com a rea excedente?"
          onChange={handleChangeInput}
        />
      </CInputGroup>
      <p className="mb-3 col-xl-9 col-sm-12 col-lg-9">
        Existe Reserva Particular do Patrimnio Natural (RPPN) no interior do
        imvel rural?
      </p>
      <CInputGroup className="mb-3 col-xl-3 col-sm-12 col-lg-3">
        <CSelect
          custom
          onChange={handleChangeInput}
          value={inputPropertyInformation.rppn}
          name="rppn"
          title="Existe Reserva Particular do Patrimnio Natural (RPPN) no interior do imvel rural?"
          id="select"
          required
        >
          <option value={undefined} hidden>
            Escolha uma opo
          </option>
          <option value={true}>Sim</option>
          <option value={false}>No</option>
        </CSelect>
      </CInputGroup>
      <p className="mb-3 col-xl-9 col-sm-12 col-lg-9">
        Possu Cota de Reverva Florestal (CRF)?
      </p>
      <CInputGroup className="mb-3 col-xl-3 col-sm-12 col-lg-3">
        <CSelect
          custom
          onChange={handleChangeInput}
          value={inputPropertyInformation.crf}
          name="rppn"
          title="Possu Cota de Reverva Florestal (CRF)?"
          id="select"
          required
        >
          <option value={undefined} hidden>
            Escolha uma opo
          </option>
          <option value={true}>Sim</option>
          <option value={false}>No</option>
        </CSelect>
      </CInputGroup>
      <CInputGroup className="mb-3 col-xl-12 col-sm-12 col-lg-12">
        <CInputGroupPrepend>
          <CInputGroupText>
            <GavelOutlined style={styles.icon} />
          </CInputGroupText>
        </CInputGroupPrepend>
        <CInput
          type="text"
          name="legislationDate"
          title="A Reserva Legal do Imvel rural est submetida  legislao de que perodo?"
          placeholder="A Reserva Legal do Imvel rural est submetida  legislao de que perodo?"
          value={inputPropertyInformation.legislationDate}
          onChange={handleChangeInput}
          required
        />
      </CInputGroup>
      <p className="mb-3 col-xl-9 col-sm-12 col-lg-9">
        Ocorreu alterao no tamanho da rea do imvel aps 22/07/2008?
      </p>
      <CInputGroup className="mb-3 col-xl-3 col-sm-12 col-lg-3">
        <CSelect
          custom
          onChange={handleChangeInput}
          value={inputPropertyInformation.pra}
          title="Ocorreu alterao no tamanho da rea do imvel aps 22/07/2008?"
          name="inputPropertyInformation"
          id="select"
          required
        >
          <option value={undefined} hidden>
            Escolha uma opo
          </option>
          <option value={true}>Sim</option>
          <option value={false}>No</option>
        </CSelect>
      </CInputGroup>
    </div>
  );
}

export default InfoDoImovel;
