import React from "react";
import { conformToMask } from "react-text-mask";

import CIcon from "@coreui/icons-react";

import {
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
} from "@coreui/react";

const IdentificacaoDoBeneficiario = ({
  inputBeneficiaryIdentity,
  setInputBeneficiaryIdentity,
}) => {
  
  const mask = [
    /[1-9]/,
    /\d/,
    /\d/,
    ".",
    /\d/,
    /\d/,
    /\d/,
    ".",
    /\d/,
    /\d/,
    /\d/,
    "-",
    /\d/,
    /\d/,
  ];

  const conformedCPF = conformToMask(inputBeneficiaryIdentity.cpf, mask, {
    guide: false,
  });

  const handleChangeInput = (event) => {
    const { name, value } = event.target;
    setInputBeneficiaryIdentity({ ...inputBeneficiaryIdentity, [name]: value });
  };

  return (
    <div className="row">
      <CInputGroup className="mb-3 col-xl-7 col-sm-12 col-lg-7">
        <CInputGroupPrepend>
          <CInputGroupText>
            <CIcon name="cil-user" />
          </CInputGroupText>
        </CInputGroupPrepend>
        <CInput
          type="text"
          name="name"
          title="Nome completo"
          placeholder="Nome completo"
          value={inputBeneficiaryIdentity.name}
          onChange={handleChangeInput}
          required
        />
      </CInputGroup>
      <CInputGroup className="mb-3 col-xl-5 col-sm-12 col-lg-5">
        <CInputGroupPrepend>
          <CInputGroupText>@</CInputGroupText>
        </CInputGroupPrepend>
        <CInput
          type="email"
          name="email"
          title="Email"
          placeholder="Email"
          value={inputBeneficiaryIdentity.email}
          onChange={handleChangeInput}
          required
        />
      </CInputGroup>
      <CInputGroup className="mb-3 col-xl-4 col-sm-12 col-lg-4">
        <CInputGroupPrepend>
          <CInputGroupText>
            <CIcon name="cil-credit-card" />
          </CInputGroupText>
        </CInputGroupPrepend>
        <CInput
          type="text"
          name="cpf"
          value={conformedCPF.conformedValue}
          placeholder="CPF"
          onChange={handleChangeInput}
          title="CPF deve conter mais de 11 nmeros."
          required
        />
      </CInputGroup>
      <CInputGroup className="mb-3 col-xl-4 col-sm-12 col-lg-4">
        <CInputGroupPrepend>
          <CInputGroupText></CInputGroupText>
        </CInputGroupPrepend>
        <CInput
          type="text"
          name="collectionCode"
          title="Cdigo de Coleta"
          placeholder="Cdigo de Coleta"
          value={inputBeneficiaryIdentity.collectionCode}
          onChange={handleChangeInput}
          required
        />
      </CInputGroup>
      <CInputGroup className="mb-3 col-xl-4 col-sm-12 col-lg-4">
        <CInputGroupPrepend>
          <CInputGroupText></CInputGroupText>
        </CInputGroupPrepend>
        <CInput
          type="text"
          name="settlement"
          title="Cdigo de Assentamento"
          placeholder="Cdigo de Assentamento"
          value={inputBeneficiaryIdentity.settlement}
          onChange={handleChangeInput}
          required
        />
      </CInputGroup>
      <CInputGroup className="mb-3 col-xl-4 col-sm-12 col-lg-4">
        <CInputGroupPrepend>
          <CInputGroupText>
            <CIcon name="cil-location-pin" />
          </CInputGroupText>
        </CInputGroupPrepend>
        <CInput
          type="text"
          name="placeOfBirth"
          title="Naturalidade"
          placeholder="Naturalidade"
          value={inputBeneficiaryIdentity.placeOfBirth}
          onChange={handleChangeInput}
          required
        />
      </CInputGroup>
      <CInputGroup className="mb-3 col-xl-4 col-sm-12 col-lg-4">
        <CInputGroupPrepend>
          <CInputGroupText>
            <CIcon name="cil-check" />
          </CInputGroupText>
        </CInputGroupPrepend>
        <CInput
          type="text"
          name="rb"
          title="Status na RB"
          placeholder="Status na RB"
          value={inputBeneficiaryIdentity.rb}
          onChange={handleChangeInput}
          required
        />
      </CInputGroup>
      <CInputGroup className="mb-3 col-xl-4 col-sm-12 col-lg-4">
        <CInputGroupPrepend>
          <CInputGroupText>
            <CIcon name="cil-map" />
          </CInputGroupText>
        </CInputGroupPrepend>
        <CInput
          type="text"
          name="incraArea"
          title="Ocupa rea destinada pelo Incra"
          placeholder="Ocupa rea destinada pelo Incra"
          value={inputBeneficiaryIdentity.incraArea}
          onChange={handleChangeInput}
          required
        />
      </CInputGroup>
    </div>
  );
};

export default IdentificacaoDoBeneficiario;
